import Axios from 'axios';

export async function UploadCustomerInfo (externalId, customerInfo) {
  const url = `${window.side_pocket_api_url}/userinfo`;
  const requestConfig = {
    headers: {
      'Content-Type': 'application/json'
    }
  };
  const body = {
    ExternalID: externalId,
    Content: customerInfo
  };

  await Axios.post(url, body, requestConfig);
}

export async function UploadFile (externalId, fileName, file) {
  const url = `${window.side_pocket_api_url}/fileurl`;
  const requestConfig = {
    headers: {
      'Content-Type': 'application/json'
    }
  };
  const body = {
    ExternalID: externalId,
    FileName: fileName,
    FileType: file.type,
    OriginalFileName: file.name
  };

  const response = await Axios.post(url, body, requestConfig);
  const presignedUrl = response.data.url;

  // Put the fileType in the headers for the upload
  var options = {
    headers: {
      'Content-Type': file.type
    }
  };
  await Axios.put(presignedUrl, file, options);
}
